.courses {
  background: #ecf4ff;
  padding-top: 80px;
  padding-bottom: 90px;
}

.courses__title {
  max-width: 674px;
}

.courses__item {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.courses__item__block {
  cursor: pointer;
  background: #fff;
  flex: 0 31%;
  margin-bottom: 30px;
  margin-right: 15px;
  padding: 24px 24px 35px;
  transition: box-shadow .2s linear;
}

.courses__item__block:hover {
  box-shadow: 0 5px 15px #0000004d;
}

.courses__item__block__image {
  height: 208px;
  width: 100%;
  margin-bottom: 24px;
  overflow: hidden;
}

.courses__item__block__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.courses__item__reactions {
  border-bottom: 1px solid #e9e8e8;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 22px;
  display: flex;
  transform: rotate(-.18deg);
}

.avatar img, .icon img {
  max-width: 100%;
}

.courses__item__reactions__item {
  margin-right: 18px;
}

.courses__item__reactions__item:last-child {
  margin-right: 0;
}

.courses__item__info {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
  display: flex;
}

.courses__item__info__what {
  color: #333;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 31px;
}

.courses__item__info__cost {
  color: #086cf9;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 31px;
}

.courses__item__text {
  color: #555;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}

.courses__slider {
  display: none;
}

.courses__button {
  text-align: center;
  margin-top: 20px;
}

.courses__button__item {
  padding: 18px 32px;
  font-weight: 500;
}

@media (max-width: 1100px) {
  .courses__item__reactions__item {
    margin-right: 8px;
  }

  .courses__item__info__what, .courses__item__info__cost {
    font-size: 20px;
    line-height: 24px;
  }
}

@media (max-width: 970px) {
  .courses__item {
    justify-content: center;
  }

  .courses__item__block {
    flex: 0 45%;
    margin-bottom: 30px;
    margin-right: 30px;
  }

  .courses {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

@media (max-width: 780px) {
  .courses__item {
    justify-content: space-between;
  }

  .courses__item__block {
    flex: 0 48%;
    margin-bottom: 30px;
    margin-right: 10px;
  }
}

@media (max-width: 610px) {
  .courses__item {
    justify-content: center;
  }

  .top {
    max-width: 420px;
  }

  .courses__item__block {
    flex: 0 65%;
    margin-bottom: 20px;
    margin-right: 0;
  }

  .none {
    display: none;
  }

  .courses__slider {
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .courses__slider__item {
    width: 12px;
    height: 12px;
    background: #fff;
    border: 1px solid #222;
    border-radius: 50%;
    margin-right: 17px;
  }

  .courses__slider__item:nth-child(2) {
    width: 14px;
    height: 14px;
    background: #086cf9;
    border: 1px solid #086cf9;
  }

  .courses__button {
    margin-top: 25px;
  }

  .courses__button__item {
    padding: 9px 15px;
  }
}

@media (max-width: 470px) {
  .courses__item__block {
    flex: 0 100%;
  }

  .courses {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

